<script>
import {
    useBornMarkAsComplete,
    useOocyteCollectionInit
} from "@/composable/born";
import {
    computed,
    inject,
    onMounted,
    onUnmounted,
    toRefs,
    watch
} from "vue-demi";
import format from "date-fns/format";
import { DATE_FORMAT_STRING } from "@/config";
import { emitter } from "@/event-bus";
import { TIME_0 } from "@/constants";
import { getFertilizatinStartDate } from "@/service/opu";
import { getPatient } from "@/service/patient-message-log";

export default {
    props: {
        isInBornPage: {
            type: Boolean,
            default: false
        }
    },
    setup(props) {
        const {
            store,
            form,
            loading,
            v$,
            save,
            fillForm,
            oocyteOriginOptions,
            donorProviderStatusOptions,
            oocyteCryoMethodOptions,
            spermOriginOptions,
            spermOriginStatusOptions,
            spermSourceOptions,
            onOocyteAssingedToChageHandler,
            onOtherCycleIdChangeHandler,
            filteredPatient,
            patientFilter,
            getOtherPrimaryCycles,
            otherPrimaryCycles
        } = useOocyteCollectionInit();

        const primaryCycles = computed(() => store.state.opu.primaryCycles);

        const { isInBornPage } = toRefs(props);

        watch(() => store.state.born.oocyteCollections, fillForm);

        watch(
            () => form.retrievedOrThawed,
            (newVal) => {
                store.commit("born/SET_OOCYTES_RETRIEVED", newVal);
            }
        );

        watch(
            () => store.state.born.primaryCycleInfo,
            (newVal) => {
                form.oocyteCollectionFrom = newVal.primaryCycleId;
                // form.otherPatientId = newVal.otherPatientId;
                form.otherPrimaryCycleId = newVal.otherPrimaryCycleId;
                form.fromOutside = newVal.fromOutside == 1 ? true : false;
                getPatient(newVal.otherPatientId)
                    .then((data) => {
                        let patient = {
                            demographicNo: data.demographic_no,
                            firstName: data.first_name,
                            lastName: data.last_name,
                            address: data.address,
                            city: data.city,
                            postal: data.postal,
                            phone: data.phone,
                            dateOfBirth: `${data.year_of_birth}-${data.month_of_birth}-${data.date_of_birth}`,
                            providerNo: data.provider_no,
                            label: `${data.demographic_no} - ${data.first_name} ${data.last_name}`
                        };

                        form.otherPatient = patient;
                        getOtherPrimaryCycles();
                    })
                    .catch((err) => {});
            }
        );

        watch(
            () => form.spermTooLowCount,
            (newVal) => {
                if (newVal == "Y") {
                    form.spermCount = null;
                }
            }
        );

        watch(
            () => form.oocyteCryoPreserved,
            (newVal) => {
                store.commit("born/SET_OOCYTE_CRYO_PRESERVED", newVal);
            }
        );

        const bornForm = inject("bornForm", null);

        watch(form, (newValue) => {
            if (bornForm == null) return;

            // console.log(form.oocyteCollectionDate);
            bornForm.oocyteCollection = {
                ...newValue,
                donorProviderSource: form.oocytesProviderSource,
                oocyteCollectionDate: form.oocyteCollectionDate
                    ? format(form.oocyteCollectionDate, DATE_FORMAT_STRING)
                    : null,
                oocyteThawDate: form.oocyteThawDate
                    ? format(form.oocyteThawDate, DATE_FORMAT_STRING)
                    : null,
                oocyteFreezeDate: form.oocyteFreezeDate
                    ? format(form.oocyteFreezeDate, DATE_FORMAT_STRING)
                    : null
            };
        });

        const isEggFreezing = computed(
            () => store.state.artCycle.selectedCycle.cycleType == "Egg Freezing"
        );

        onMounted(() => {
            store.dispatch("opu/getPrimaryCycles");

            emitter.on("born:save-temp", save);

            getFertilizatinStartDate(
                store.state.artCycle.selectedCycle.cycleId
            ).then((resp) => {
                const { startDate } = resp;
                if (startDate && form.oocyteCollectionDate == null) {
                    form.oocyteCollectionDate = new Date(startDate + TIME_0);
                }
            });

            store
                .dispatch(
                    "fertilization/getFertilization",
                    store.state.artCycle.selectedCycle.cycleId
                )
                .then(() => {})
                .catch(() => {});

            if (isInBornPage.value) {
                return;
            }

            store.dispatch(
                "born/getBronV2",
                store.state.artCycle.selectedCycle
            );

            store.dispatch(
                "fertilization/getFertilization",
                store.state.artCycle.selectedCycle.cycleId
            );
        });

        onUnmounted(() => {
            emitter.off("born:save-temp", save);
        });

        const { markAsComplete } = useBornMarkAsComplete();

        return {
            isInBornPage,
            form,
            loading,
            save,
            v$,
            fillForm,
            isEggFreezing,
            oocyteOriginOptions,
            donorProviderStatusOptions,
            oocyteCryoMethodOptions,
            spermOriginOptions,
            spermOriginStatusOptions,
            spermSourceOptions,
            markAsComplete,
            primaryCycles,
            onOocyteAssingedToChageHandler,
            onOtherCycleIdChangeHandler,
            filteredPatient,
            patientFilter,
            getOtherPrimaryCycles,
            otherPrimaryCycles
        };
    }
};
</script>

